import Vue from 'vue'
import Router from 'vue-router'
import firebase from 'firebase'

import Comics from '../components/Comics'
import CampAdmin from '../components/CampAdmin'
import CampAdminList from '../components/CampAdminList'
import Login from '../components/Login'
import SignUp from '../components/SignUp'
import Planner from '../components/Planner'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp
    },
    {
      path: '/comics',
      name: 'Comics',
      component: Comics,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/planner',
      name: 'Planner',
      component: Planner,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/campadmin/:id',
      name: 'CampAdmin',
      props: true,
      component: CampAdmin,
      meta: {
        requiresAuth: true
      },
    },
      {
        path: '/campadmin',
        name: 'CampAdminList',
        props: true,
        component: CampAdminList,
        meta: {
          requiresAuth: true
        }
      }
  ]
})

router.beforeEach((to, from, next) => {
  let currentUser = firebase.auth().currentUser
  let requiresAuth = to.matched.some(record => record.meta.requiresAuth)
  console.log(requiresAuth + " current user " + currentUser)

  // if (requiresAuth && !currentUser) next('login')
  // else if (!requiresAuth && currentUser) next('comics')
  // else next()
  next()
})

export default router



