<template>
  <div>
    Plan all of your summercamps here
    <img
      src="../assets/kidprofile.png"
      width="50em"
      style="v-align:middle"
    >
    add a child {{currentUserId}}
    <form @submit="addChild">
      <input v-model="newChild.name" required>
    </form>
    <ChildPlanner style = "clear:both"
      :child="child.name"
      v-for="(child, idx) in mykids"
      :key="idx"
      :allcamps="allcamps"
    >inside</ChildPlanner>
  </div>
</template>
<script>
import firebase from "firebase";
import { db } from "../main";
import ChildPlanner from "./ChildPlanner.vue";

export default {
  name: "Planner",
  fiery: true, // required to add this.$fiery to this component
  components: { ChildPlanner },
  methods: {
    addChild() {
      this.newChild.created_by = this.currentUserId;
      db.collection("kids").add(this.newChild);
      this.newChild.name = "";
      this.newChild.grade = "";
    }
  },
  created: function() {
    console.log("user is " + firebase.auth.user);
    var router = this.$router;
    var mythis = this;
    firebase.auth().onAuthStateChanged(function(user) {
      console.log("user  is " + firebase.auth.user);

      if (user) {
        mythis.currentUserId = user.uid;
        mythis.mykids = mythis.$fiery(db.collection("kids/"), {
          query: kids => kids.where("created_by", "==", mythis.currentUserId)
        });
        console.log(mythis.mykids)
      }
    });
  },
  data() {
    return {
      newChild: { name: "", grade: "K" },
      children: ["Quill"],
      currentUserId: "",
      mykids: this.currentUserId
        ? this.$fiery(db.collection("kids/"), {
            query: kids => kids.where("created_by", "==", this.currentUserId)
          })
        : {},
      allcamps: this.$fiery(db.collection("camps/"), {
        sub: {
          programs: {
            sub: {
              sessions: {
                // creates an array or map on each todo object: todo.children[]
                // once, map, etc
                // query: children => children.orderBy('updated_at')
              }
            }
          }
        }
      })
    };
  }
};
</script>
<style scoped>
</style>



