<template>
  <div class="card" v-if="programsThisWeek.length > 0 || edit">
    <b @click="collapsed = !collapsed" style="width:100%">{{camp.name}} <span v-if="collapsed">({{programsThisWeek.length}})</span></b>
    <div v-if="!collapsed">
      <hr>
      <div v-for="(program, idx) in programsThisWeek" :key="idx" class="onesession">
        <span style="font-size:20px">✩</span><span v-if="false"> ★</span>
        {{program.name}}
        <div v-for="(session,idx) in program.sessions" :key="idx">
          <span v-if="edit" @click="remove(session)">x</span>
        </div>
        <datepicker v-if="edit" use-utc v-model="mydate" placeholder="Start Date"></datepicker>
        <button v-if="edit" @click="addsession(program)">add</button>
      </div>
    </div>
  </div>
</template>


<script>
import firebase from "firebase";
import { db } from "../main";
import FieryVue from "fiery-vue";
import Datepicker from "vuejs-datepicker";

export default {
  name: "CampWeekProgramCard",
  fiery: true,
  props: ["camp", "week", "edit", "fire", "collapsed"],
  components: { Datepicker },
  computed: {
    programsThisWeek: function() {
      var myprograms = [];
      this.camp.programs.forEach(program => {
        if (!this.week) {
          myprograms.push(program);
        } else {
          program.sessions.forEach(session => {
            if (this.sessioninweek(session)) {
              myprograms.push(program);
            }
            // break;
          });
        }
      });
      return myprograms;
    }
  },
  data() {
    return {
      mydate: ""
    };
  },
  methods: {
    remove(thisthing) {
      console.log(this + thisthing[".uid"]);
      this.fire.remove(thisthing);
    },
    addsession(program) {
      this.fire.ref(program, "sessions").add({ date: this.mydate });
    },
    sessioninweek(session) {
      return (
        session.date.seconds >= this.week.seconds &&
        session.date.seconds < this.week.seconds + 60 * 60 * 24 * 7
      );
    }
  }
};
</script>
<style scoped>
.card {
  border-radius: 10px;
  border: 1px solid #aaa;
  padding: 10px;
  background: #fafafa;
  width: 150px;
  vertical-align: top;
  text-align: left;
      /* background-color: rgb(228, 0, 43);
      color: white; */
      /* font-color:white; */

}
.onesession {
  font-size: 14px;
}
</style>


