<template>
  <div>
    <button class="button logout" v-on:click="logout">Logout</button>
    {{ camp.name }}
    <article class="covers" v-for="(program, idx) in camp.programs" :key="idx">
      <div>
        <!-- <img style="margin: 10px" :src="program.image" height="291px" width="192px"> -->
        <p>{{ program.name }}</p>
        <photoshop-picker :value="color" @input="updateValue"></photoshop-picker>

        <!-- <div class="card" v-for="(value, key) in program" :key="key">{{ key }} <hr> {{ value}}</div>
        <hr>-->
        <button class="button" @click="deleteProgram(program)">Delete</button>
      </div>
    </article>

    <CampWeekProgramCard :camp="camp" :edit="true" :fire="this.$fiery"></CampWeekProgramCard>
    <form @submit="addProgram(newprogram)">
      <h2>Add a New Program</h2>
      <input v-model="newprogram.name" placeholder="Program Name" class="input" required>
      <input v-model="newprogram.url" placeholder="Program URL" class="input" required>
      <input
        v-model="newprogram.description"
        placeholder="Program Description"
        class="input"
        required
      >
      <input v-model="newprogram.cost" placeholder="Cost" class="input" required>
      <button type="submit" class="button">Add New Program</button>
    </form>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "../main";
import FieryVue from "fiery-vue";
import CampWeekProgramCard from "./CampWeekProgramCard.vue";
import { Photoshop } from 'vue-color'

export default {
  name: "CampAdmin",
  components: { CampWeekProgramCard,     'photoshop-picker': Photoshop,
 },
  fiery: true, // required to add this.$fiery to this component
  props: ["id"],
  data() {
    return {
      color: "#000",
      camp: this.$fiery(db.collection("camps/").doc(this.id), {
        sub: {
          programs: {
            sub: {
              sessions: {
                // creates an array or map on each todo object: todo.children[]
                // once, map, etc
                // query: children => children.orderBy('updated_at')
              }
            }
          }
        }
      }),
      newprogram: {
        name: "",
        url: "",
        // description: "",
        image: ""
      }
    };
  },
  methods: {
    addProgram(program) {
      const createdAt = new Date();
      db.collection("camps/" + this.id + "/programs").add(program);
      // Clear values
      this.newprogram = {}
  
    },
    updateValue() {

    },
    addSession(program, session) {

    },
    deleteProgram(program) {
      this.$fiery.remove(program);
      //   db.collection("camps/"+this.id+"/programs")
      //     .doc(program[".uid"])
      //     .delete();
    },
    remove(thing) {
        // alert(thing.ref) 
        this.$fiery.remove(thing[".uid"])
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace("login");
        });
    }
  }
};
</script>
<style scoped>
.card {
  border-radius: 10px;
  border: 1px solid #aaa;
  padding: 20px;
}
</style>


