<template>
  <div>
    Hello
    {{child}}
    <div class="container">
      <div v-for="week in weeks" :key="week.name" :class="'week week'+week.day">
        {{week.name}}
        <CampWeekProgramCard :camp="camp" :week="week" v-for="(camp, idx) in allcamps" :key="idx"></CampWeekProgramCard>
        <!-- <CampWeekProgramCard :camp="camp" :week="week" v-for="(camp, idx) in allcamps" :key="idx"></CampWeekProgramCard> -->
      </div>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
import { db } from "../main";
import FieryVue from "fiery-vue";
import CampWeekProgramCard from "./CampWeekProgramCard.vue";

export default {
  name: "ChildPlanner",
  props: ["child", "allcamps"],
  components: { CampWeekProgramCard },
  computed: {
    weeks() {
      var startDate = new Date(Date.UTC(2019, 5, 3));
      var monthNames = [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December"
      ];

      var myweeks = [];
      for (var i = 0; i < 12; i++) {
        var week = {};
        week.name =
          monthNames[startDate.getUTCMonth()] + " " + startDate.getUTCDate();
        week.day = startDate.getUTCDate();
        week.seconds = startDate.getTime() / 1000;
        myweeks.push(week);
        var ms = startDate.getTime() + 1000 * 60 * 60 * 24 * 7;
        startDate = new Date(ms);
      }
      return myweeks;
    }
  }
};
</script>
<style scoped>
.card {
  border-radius: 10px;
  border: 1px solid #aaa;
  padding: 20px;
  background: #fafafa;
  width: 100px;
  margin-bottom: 10px;
  /* height:150px; */
}
.container {
  /* columns: 12; */
  /* column-width:150px; */
  width: 1464px;
}
.week {
  /* background-color:blue;  */
  width: 180px;
  /* display: inline-block; */
  vertical-align: top;
  float:left;
}
.week1,
.week2,
.week3,
.week4,
.week5,
.week6,
.week7 {
  content: "first";
  clear:both;
}
</style>


